import React, { useEffect, useState } from 'react';
import { Combobox, TextInput, useCombobox, InputBase } from '@mantine/core';

import cities from './cities';

export default function LocationSearchBar() {
  const combobox = useCombobox({
    onDropdownClose: () => combobox.resetSelectedOption(),
  });

  const [value, setValue] = useState('');
  const shouldFilterOptions = !cities.some((item) => item === value);
  let filteredOptions = shouldFilterOptions
    ? cities.filter((item) => item.toLowerCase().includes(value.toLowerCase().trim()))
    : cities;

  const filteredOptionsTrueLength = filteredOptions.length;  // The number of options after filtering but before truncating

  const maxOptionsToDisplay = 5;
  filteredOptions = filteredOptions.slice(0, maxOptionsToDisplay)  // Now truncate to some number

  let options = filteredOptions.map((item) => (
    <Combobox.Option
      value={item}
      key={item}
    >
      {item}
    </Combobox.Option>
  ));
  if ((filteredOptions.length > 0) && (filteredOptionsTrueLength > maxOptionsToDisplay)) {
    options.push(
      <Combobox.Option
        value={"..."}
        key={"..."}
        disabled={true}
      >
        {"..."}
      </Combobox.Option>
    )
  }

  useEffect(() => {
    // we need to wait for options to render before we can select first one
    combobox.selectFirstOption();
  }, [value]);

  return (
    <Combobox
      onOptionSubmit={(optionValue) => {
        setValue(optionValue);
        combobox.closeDropdown();
      }}
      store={combobox}
      withinPortal={false}
      size='md'
    >
      <Combobox.Target>
        <TextInput
          //   label="Pick value or type anything"
          placeholder="🔍 Search location"
          value={value}
          onChange={(event) => {
            setValue(event.currentTarget.value);
            combobox.openDropdown();
          }}
          onClick={() => combobox.openDropdown()}
          onFocus={() => combobox.openDropdown()}
          onBlur={() => combobox.closeDropdown()}
          styles={{
            root: {
              padding: '0',
            },
            input: {
              // padding: '12px 16px',
              borderRadius: '100px',
            },
          }}
        />
      </Combobox.Target>

      <Combobox.Dropdown>
        <Combobox.Options>
          {options.length === 0 ? <Combobox.Empty>Nothing found</Combobox.Empty> : options}
        </Combobox.Options>
      </Combobox.Dropdown>
    </Combobox>
  );
}